.Report{width:100%;height:100%;position:relative;border-radius:10px;overflow:hidden;color:#000;font-family:"국립박물관문화재단클래식B",sans-serif}.Report .title{position:relative;z-index:10;color:#000;font-weight:bold}.Report .textContent{position:absolute;left:50%;transform:translateX(-50%);bottom:2vh;width:100%;z-index:10;color:#000;font-size:1em;line-height:1.5em;padding-bottom:20px;text-align:center}.Report .mainContent{font-weight:bold;background-color:#ef9;line-height:1.8em;padding:.5em .7em;border-radius:10px;color:#000}.Report .point{font-family:"KOTRAHOPE",sans-serif;font-size:1.4em;color:#f22}