// myStyle.module.scss
$box-shadow: 0 6px 12px rgba(0, 0, 0, 0.3);

.bankAccount {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  padding: 1em 1.5em;
  padding-bottom: 2em;
  gap: 2.2em;
  .item1{
    flex: 5;
    display: flex;
    flex-direction: row;
    height: 100%;
    width: 100%;
    justify-content: space-between;
    gap: 2.5em;

    .left, .right {
      flex: 5;
      display: flex; // 추가
      justify-content: center; // 가로 가운데 정렬
      align-items: center; // 세로 가운데 정렬
      height: 100%;
      width: 100%;
    }
    
    .left {
      .card{
        background-color: rgba(160, 169, 245 , 0.3); // right의 색상
      }
    }

    .right {
      .card{
        background-color: rgba(241, 158, 241, 0.3); // right의 색상
      }
    }
    
  }
  
  .item2{
    flex: 2;
    display: flex;
    justify-content: center; // 가로 가운데 정렬
    align-items: center; // 세로 가운데 정렬
    // background-color: #00a;
    width: 100%;
    .card{
      background-color: rgba(125, 212, 107, 0.3); // right의 색상
    }
  }
  
}

.card {
  display: flex;
  text-align: center; // 텍스트 가운데 정렬
  // background-color: #444;
  // padding: 0.8em 0.5em;
  border-radius: 12px;
  box-shadow: $box-shadow;
  height: 100%;
  width: 100%;
 
  align-items: center;
  justify-content: center;

  .accountItem {
    flex: 1;
    width: 100%;
    display: flex;
    flex-direction: column;
    line-height: normal;

    .title{
      flex: 1;
      font-size: 1.5em;
      font-weight: bold;
      .bank{
        font-size: 0.7em;
      }
    }

    .account{
      flex: 1;
      font-size: 1em;
      margin: 0.2em;
    }      

    .copyButton {
      font-family: 'KOTRAHOPE', sans-serif; // Or similar brush fon
      flex: 1;
      padding: 5px 10px;
      background-color: #444;
      color: white;
      border: none;
      border-radius: 4px;
      cursor: pointer;
    }
  }


}
