// myStyle.module.scss
.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  padding: 1rem;

  .title {
    flex: 1;
    font-size: 1.2rem;
    margin-bottom: 10px;
    text-align: center;
  }
  
  .main {
    flex: 6;
    width: 100%;
    display: flex;
    flex-direction: column;


    .mapView{
      display: flex;
      flex-direction: row;
      flex: 7;
      gap: 1em;
      padding: 0em 1em;
      // background-color: rgba(150, 238, 146, 0.6); // right의 색상
      position: relative; /* 자식 요소의 위치를 지정할 수 있도록 설정 */
      overflow: hidden; /* 지도 바깥의 내용을 숨김 */
      .map{
        flex: 5;
        height: 100%;
        border: 0.5px solid rgba(145, 136, 57, 0.8); /* 굵기 2px, 실선, 색상 검정 */
        border-radius: 25px;
      }
      .mapIcon{
        height: 100%;
      }
    }
    .content{
      flex: 3;
      // background-color: rgba(224, 238, 146, 0.7); // right의 색상
    }


  }
}


