.footer{
  display: flex;
  flex-direction: column;
	align-items: center;
	justify-content: center;
	height: inherit;
  .top{
    flex: 4;
    width: 100%;
    align-items: center;
    justify-content: center;
    display: flex;
    flex-direction: row;

    .left{
      flex: 3;
      text-align: right; 
      .leading{
        text-align: right; 
        display: block; 
      }
    }
    .center{
      flex: 1;
      font-size: 1.5em;
      
    }
    .right{
      flex: 3;
      text-align: left; 
      .leading{
        text-align: left; 
        display: block; 
      }
    }

    .parents{
      font-size: 1.2em;
      font-weight: bold;
    }
    .child{
      background-color: #c49d6e;
      border-radius: 0.5em;
      padding: 0.2em;
      font-size: 0.8em;
    }
    .leading{
      font-size: 1.4em;
      line-height: 1.2em;
      font-weight: bold;
      // font-family: 'SF_HambakSnow', sans-serif; 
    }
  }

  .middle{
    display: block;
    padding: 0.2em;
    flex: 5;
    width: 100%;
    color: #6f4f28;
    // font-weight: bold;
    font-size: 1.6em;
    align-items: center;
    justify-content: center;
    // text-decoration: underline;
  }

  .bottom{
    font-family: 'Ownglyph_ryurue-Rg', sans-serif; 
    // flex: 1;
    width: 100%;

  }
}

.red{
  // font-family: 'KOTRAHOPE', sans-serif; 
  font-family: 'Ownglyph_ryurue-Rg', sans-serif; 
  color: #f33;
  font-weight: bold;
  font-size: 1.2em;
}
.black{
  // font-family: 'KOTRAHOPE', sans-serif; 
  // color: #000;
  font-size: 1.1em;
}

.point{
  font-family: 'Ownglyph_ryurue-Rg', sans-serif; 
  font-weight: bold;
  color: #b67e36;
  display: block;
  margin-top: 0.1em;
  font-size: 1.5em;
  line-height: 1.1em;
}

