.imageContainer {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  z-index: 10;
}

.bkIndex{
  z-index: -1;
}

.image {
  height: 100%;
  width: 100%;
  object-fit: cover;
  position: absolute; /* Z축에서 쌓이도록 설정 */

}


.centerText {
  position: absolute;
  align-items: center; // 수직 정렬
  justify-content: center; // 수평 정렬
  z-index: 3;

  top: 20vh;
  @media (max-height: 600px) { top: 10vh; }

  text-align: center;
  height: 100%;
  width: 100%;
  color: #000;

  font-size: 2em;

  .top{
    padding: 20px;
    margin: 20px;
    opacity: 0;
    animation: fadeInScale 5s forwards 1.5s; /* 삼각형 애니메이션 후 텍스트 나타나도록 딜레이 추가 */
  }
  .center{

    opacity: 0;
    padding: 20px;
    margin: 20px;
    animation: fadeInScale 6s forwards 1.5s; /* 삼각형 애니메이션 후 텍스트 나타나도록 딜레이 추가 */

  }
  .bottom{
    opacity: 0;
    padding: 20px;
    margin: 20px;
    animation: fadeInScale 5s forwards 1.5s; /* 삼각형 애니메이션 후 텍스트 나타나도록 딜레이 추가 */

  }
}

/* 애니메이션 정의 */
@keyframes fadeInScale {
  0% {
    opacity: 0;
    transform: scale(0.8); /* 작게 시작 */
  }
  25% {
    opacity: 0.5;
    transform: scale(1.7); /* 살짝 커졌다가 */
  }
  50% {
    opacity: 1;
    transform: scale(1); /* 원래 크기로 */
  }
  75% {
    opacity: 0.5;
    transform: scale(0.8); /* 살짝 커졌다가 */
  }
  100% {
    opacity: 0;
    transform: scale(0.5); /* 작게 시작 */
  }
}

